*,
*::after,
*::before {
  box-sizing: inherit;
}

/*        (ul) */
ul {
  list-style: none;
  padding-left: 0;
  margin: 0;
}

/*        */
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin-top: 0;
  margin-bottom: 0;
}

/*        */
a {
  text-decoration: none;
  color: inherit;
}

/*      */
img {
  display: block;
  width: 100%;
  height: auto;
}

/*      address */
address {
  font-style: normal;
}
